@font-face {
	font-family: 'Myanmar3';
	src: url(../font/Myanmar3.eot); /* IE9 Compat Modes */
	src: url(../font/Myanmar3.eot?#iefix) format('embedded-opentype'), /* IE6-IE8 */
	     url(../font/Myanmar3.woff) format('woff'), /* Modern Browsers */
	     url(../font/Myanmar3.ttf)  format('truetype'), /* Safari, Android, iOS */
	     url(../font/Myanmar3.svg#Myanmar3) format('svg'); /* Legacy iOS */
	unicode-range: U+1000-109F, U+AA60-AA7B;
	font-weight: normal;
	font-style: normal;
}

